<template>
  <div class="container"
    :style="{left:editorData.left+'px',top:editorData.top+'px'}">
    <div class="title">
      <span>编辑关系</span>
    </div>
    <div class="content">
      <div class="base">
        <span>基数</span>
        <Selection class="selections"
          @selectValue="selectionsEvent"
          v-bind="{selections}"></Selection>
      </div>
      <div class="relation"
        v-show="false">
        <span>交叉筛选器方向</span>
        <select class="form-control">
          <option value="2">两个</option>
        </select>
      </div>
    </div>
    <div class="btnDiv">
      <span class="determine mainBtn"
        @click.stop="editorData.determineFun(choose)">确定</span>
      <span class="cancel mainBtn"
        @click.stop="editorData.cancelFun">取消</span>
    </div>
  </div>
</template>
<script>
import Selection from '@/components/Selection/Selection.vue';

export default {
	name: 'modifyConnection',
	props: ['editorData'],
	components: { Selection },
	data() {
		return {
			choose: null,
			selections: {
				placeholder: '',
				w: '10.5vw',
				inputVal: this.editorData.type[1],
				data: [
					{
						label: '一对一', //展示的文本
						value: 'INNER' //值
					},
					{
						label: '一对多',
						value: 'RIGHT'
					},
					{
						label: '多对一',
						value: 'LEFT'
					},
					{
						label: '多对多',
						value: 'FULL'
					}
				]
			}
		};
	},
	methods: {
		selectionsEvent(data) {
			this.choose = data;
		}
	},
	created() {
		this.selections.inputVal = this.editorData.type;
	}
};
</script>
<style scoped>
.container {
  width: 12.3vw;
  background-color: #1c2433;
  position: fixed;
  padding: 1vh 0.9vw;
  color: #fff;
  z-index: 10;
}

.container .title {
  font-size: 0.6vw;
  margin-bottom: 1vh;
}

.container .content select {
  width: 10.5vw;
  height: 2.5vh;
  background-color: #121721;
  border: solid 0.0625vw #475566;
  color: #fff;
  padding: 0 0.5vw;
  margin-top: 1vh;
  margin-bottom: 0.8vh;
}

.container .content select:focus {
  outline: solid 0.0625vw #28b3fc;
  outline-offset: 0;
}

.container .content .base >>> .selection-component .selection-list {
  top: 2.7vh;
}

.container .content .base >>> .triangleImg {
  top: 0.9vh;
}

.container .btnDiv {
  text-align: right;
  margin: 2vh 0;
}

.container .btnDiv span {
  font-size: 0.6vw;
  padding: 0.5vh 0.9vw;
}

.container .btnDiv .determine {
  margin-right: 0.5vw;
}
</style>
