<template>
  <div style="overflow: hidden">
    <div class="tableRelation"
      id="tableRelation"
      ref="tableRelation">
      <div class="tableContent"
        ref="tableContent"
        id="tableContent"
        :style="{transform:`scale(${scaleNum})`}">
        <div class="tableInfo"
          v-for="(data,i) in dataModeling"
          :key="data.source_sn"
          :id="data.source_sn"
          :ref="data.source_sn"
          :style="[setTableSize(data.position)]"
          :class="{activeTablea:chosen==data.source_sn,zIndex:zIndexDrag}"
          @mouseenter="chosen=data.source_sn"
          @mouseleave="chosen=null">
          <div class="tableTitle">
            <i class="icon dataSource unSelect titleIcon"
              :class="'icon-'+data.db_source_type"></i>
            <span class="unSelect">{{data.db_source_name}}</span>
            <i class="icon biFont icon-clear close"
              @click="deleteData(data)"></i>
          </div>
          <div class="listInfo"
            :style="setListSize(data.position)"
            :ref="data.source_sn+data.db_source_name">
            <ul>
              <li class="unSelect"
                v-for="(info,index) in data.field"
                :key="index"
                :id="data.source_sn+data.db_source_name+info.Field"
                :ref="data.source_sn+data.db_source_name+info.Field"
                :type="info.Type"
                :class="{hideLi:info.fieldShow}"
                @mouseenter.stop="enterEvent(info.Type,data.source_sn+data.db_source_name+info.Field)"
                @mouseleave.stop="leaveEvent(data.source_sn+data.db_source_name+info.Field,$event)">
                <i class="icon dataSource listIcon unSelect"
                  :class="'icon-'+info.Type"></i>
                <span>{{info.Field}}</span>
              </li>
            </ul>
          </div>
          <div v-for="handle in handles"
            :key="handle"
            v-show="chosen==data.source_sn"
            class="handle"
            :class="'handle-'+handle"
            :style="{transform:`scale(${1/scaleNum})`}"
            @mousedown.stop.prevent="resizable($event,data,handle,i)"
            @touchstart.stop.prevent="resizable($event,data,handle,i)"></div>
        </div>
      </div>
    </div>
    <!-- 缩放 -->
    <div class="zoom">
      <div class="zoomRight">
        <div class="zoomDropdown">
          <div class="zoomSelect">
            <label class="selectInput"
              v-click-outside="{fn:hideOptions}"
              @click="toggleOptionsShow">
              <span class="triangleDown"
                :class="{'zoomRotate':isRotate}"></span>
              <input type="text"
                class="unSelect"
                :class="{'selectColor':isRotate}"
                v-model="scalePercentage"
                disabled>
            </label>
            <ul class="option"
              @click.stop
              v-show="isRotate">
              <EasyScroll :barOption="reactiveScrollOpt">
                <li v-for="(item,index) in scaleList"
                  :key="index"
                  @click="setScaleValue(item.scaleValue)">
                  {{item.scaleValue}}%</li>
              </EasyScroll>
            </ul>
          </div>
        </div>
        <div class="zoomRegulation">
          <div class="sliderBtn"
            @click="zoomSubtract">
            <span class="zoomSubtract"></span>
          </div>
          <slider-range :valueRange="canvasScaleValue"
            :minValue="minScale"
            :maxValue="maxScale"
            type='canvasScale'
            @rangedata="getScaleValue"></slider-range>
          <div class="sliderBtn"
            @click="zoomAdd">
            <span class="zoomAdd"></span>
          </div>
        </div>
      </div>
    </div>
    <Modify v-bind="{editorData}"
      v-if="editorShow"
      v-click-outside="{fn:hideEditor,arg:editorShow}"></Modify>
    <ModifyDelete :options="msgData"
      v-if="modifyDeleteShow"
      v-click-outside="{fn:hideModifyDelete,arg:modifyDeleteShow}">
    </ModifyDelete>
<!--    <Tips v-bind="{tipsData}"-->
<!--      ref="tips"></Tips>-->
    <Confirm :options="confirmData"
      v-if="confirmShow"></Confirm>
  </div>
</template>
<script>
import ModifyDelete from '@/pages/DataModeling/components/ModifyDelete';
import Modify from '@/pages/DataModeling/components/ModifyConnection';
import right from '@/assets/DataModeling/img/right.png';
import inner from '@/assets/DataModeling/img/inner.png';
import Confirm from '@/components/Confirm/Confirm.vue';
import full from '@/assets/DataModeling/img/full.png';
import left from '@/assets/DataModeling/img/left.png';
import SliderRange from '@/components/SliderRange';
// import Tips from '@/components/Tips/Tips.vue';
import { mapGetters } from 'vuex';
import {jsPlumb } from 'jsplumb';

const judgeType = status => {
	const actions = new Map([
		['RIGHT', [right, '一对多']],
		['INNER', [inner, '一对一']],
		['FULL', [full, '多对多']],
		['LEFT', [left, '多对一']],
		['default', [inner, '一对一']]
	]);
	let action = actions.get(status) || actions.get('default');
	return action;
};

export default {
	name: 'tableRelation',
	props: ['chosenTable'],
	computed: {
		...mapGetters('dataSource', {
			getDataObj: 'getDataSource',
			getRelationData: 'getRelationData',
			getZoomNum: 'getZoomNum',
			getModelPage: 'getModelPage',
			getConnectEnd: 'getConnectEnd'
		}),
		...mapGetters('main', ['getVw', 'getVh', 'vwToPx', 'vhToPx']),
		reactiveScrollOpt() {
			return {
				barColor: '#95A0B6', //滚动条颜色
				railColor: '#475566', //导轨颜色
				barOpacityMin: 0.3, //滚动条非激活状态下的透明度
				barWidth: this.getVw(4),
				barMarginRight: 0
			};
		},
	},
	components: {
		// Tips,
		Modify,
		Confirm,
		SliderRange,
		ModifyDelete
	},
	data() {
		return {
		  isBorderShow:false,
			handles: ['tl', 'tm', 'tr', 'mr', 'br', 'bm', 'bl', 'ml'], //循环显示缩放的边界
			token: localStorage.getItem('token'),
			dataModeling: [], //存放表格数据
			relation: null, //存放关联线数据
			scaleNum: 1, //当前缩放值
			finalHeight: null, //存放最终高
			finalWidth: null, //存放最终宽
			finalLeft: null, //存放最终到左边距离
			finalTop: null, //存放最终到顶部距离
			chosen: null, //存放选中的id
			DragChosen: null, //存放拖拽选中的id
			zIndexDrag: true, //判断拖拽时表格zindex改变
			instanceId: {}, //存放关联表格名称，字段名称，id，以便获取参数
			instance: null, //存放关联实例
			instanceOut: true, //判断是否添加取消样式
			correlationInfo: null, //存放关联信息，以便点击其他消失样式
			isRotate: false, //缩放下拉框是否旋转
			scalePercentage: 10, //默认缩放值，百分比进度提示数字
			canvasScaleValue: 100, //存放百分比进度，默认值100%
			returnScaleValue: 0, //存放百分比进度当前值
			minScale: 10, //最小缩放值
			maxScale: 100, //最大缩放值
			isInitialize: true, // 是否是初始化
			lastScalingvalue: null, // 存放上一次的缩放值，在缩放请求失败的时候调用
			confirmShow: false, //询问框是否显示
			editorShow: false, //询问框是否显示
			modifyDeleteShow: false, //询问框是否显示
			connLine: [], //存放连接的线
			msgData: {}, //修改删除数据，调用组件
			editorData: {}, //修改关联框数据
			//提示框数据
			tipsData: {
				text: '',
				rel: '',
				time: 1500,
				repeat: ''
			},
			//询问框数据
			confirmData: {
				title: '你确定删除这个数据吗？',
				btn: [
					{
						text: '确认',
						callback: () => {}
					},
					{
						text: '取消',
						callback: () => {}
					}
				]
			},
			scaleList: [
				//缩放数据
				{
					scaleValue: 100
				},
				{
					scaleValue: 90
				},
				{
					scaleValue: 80
				},
				{
					scaleValue: 70
				},
				{
					scaleValue: 60
				},
				{
					scaleValue: 50
				},
				{
					scaleValue: 40
				},
				{
					scaleValue: 30
				},
				{
					scaleValue: 20
				},
				{
					scaleValue: 10
				}
			]
		};
	},
	methods: {
		requestData() {
			let self = this;
			return new Promise(function(resolve) {
				self.dataModeling = [];
				self.scaleNum = parseFloat(self.getZoomNum);
				if (self.scaleNum <= 1 && self.scaleNum >= 0.1) {
					self.canvasScaleValue = self.scaleNum * 100;
					self.lastScalingvalue = self.scaleNum;
				} else {
					self.scaleNum = 1;
					self.getScaleValue(100);
				}
				let res = JSON.parse(JSON.stringify(self.getDataObj));
				for (let obj in res) {
					if (res[obj].db_status != 0) {
						res[obj].position.height = parseInt(
							self.getVh(res[obj].position.height)
						);
						res[obj].position.width = parseInt(
							self.getVw(res[obj].position.width)
						);
						res[obj].position.offset_x = parseInt(
							self.getVh(res[obj].position.offset_x)
						);
						res[obj].position.offset_y = parseInt(
							self.getVw(res[obj].position.offset_y)
						);
						let fields = res[obj].field;
						for (let index in fields) {
							fields[index].fieldShow = false;
						}
						self.dataModeling.push(res[obj]);
					}
				}
				resolve();
			});
		},
		enterEvent(type, tag) {
			let item = this.$refs[tag][0];
			this.addLiStyle(type, item);
		},
		leaveEvent(tag) {
			let item = this.$refs[tag][0];
			item.style.background = 'transparent';
			item.style.border = 'solid 0.0625vw transparent';
		},
		hideModifyDelete() {
      this.isBorderShow = false;
      this.correlationInfo.setPaintStyle({ stroke: '#95a0b6'});
			this.instanceOut = true;
			this.modifyDeleteShow = false;
			this.ancelStyle(this.correlationInfo);
		},
		hideEditor() {
      this.instanceOut = true;
			this.editorShow = false;
			this.isBorderShow = false;
      this.correlationInfo.setPaintStyle({ stroke: '#95a0b6'});
			this.ancelStyle(this.correlationInfo);
		},
		deleteData(data) {
			this.chosen = data.id;
			this.confirmShow = true;
			this.confirmData.btn[0].callback = () => {
				//提示窗口确定按钮
				this.$post('api/delUploadSource', {
					source_sn: data.source_sn,
					id: data.id
				}).then(
					response => {
						let tipStatus;
						if (response.code == '000000') {
							tipStatus = 'success';
							this.$emit('uploadTable', 'success');
						} else {
							tipStatus = 'fail';
						}
						this.tips.show([response.err_msg.split(':')[1],tipStatus]);
						// this.tipsData.text = response.err_msg.split(':')[1];
						// this.$refs.tips.closeTips();
						this.confirmShow = false;
					},
					error => {
						console.log(error.response.data);
					}
				);
			};
			this.confirmData.btn[1].callback = () => {
				//提示窗口取消按钮
				this.confirmShow = false;
			};
		},
		recoveryPosition(posi, name, listName) {
			let self = this;
			if (posi) {
				self.$set(
					self.$refs[name][0].style,
					'left',
					parseInt(posi.offset_x) + 'px'
				);
				self.$set(
					self.$refs[name][0].style,
					'top',
					parseInt(posi.offset_y) + 'px'
				);
				self.$set(
					self.$refs[name][0].style,
					'width',
					parseInt(posi.width) + 'px'
				);
				self.$set(
					self.$refs[name][0].style,
					'height',
					parseInt(posi.height) + 'px'
				);
				// self.$set(
				// 	self.$refs[listName][0].style,
				// 	'height',
				// 	parseInt(posi.height) + 'px'
				// );
				self.$set(
					self.$refs[listName][0].style,
					'height',
					parseInt(posi.height - 35) + 'px'
				);
				self.instance.repaintEverything();
			}
		},
		positionSave(data, posi, name, listName) {
			let self = this;
			self.$put('api/updatePosition', data).then(
				response => {
					if (response.code != '000000') {
						self.recoveryPosition(posi, name, listName);
						this.tips.show([response.err_msg.split(':')[1]]);
						// self.tipsData.text = response.err_msg.split(':')[1];
						// self.tipsData.rel = 'fail';
						// self.$refs.tips.closeTips();
					}
				},
				error => {
					console.log(error);
					this.tips.show(['修改位置失败']);
					// self.tipsData.text = '修改位置失败';
					// self.tipsData.rel = 'fail';
					// self.$refs.tips.closeTips();
					self.recoveryPosition(posi, name, listName);
				}
			);
		},
		resizable(el, data, mark, index) {
			if (el.stopPropagation) el.stopPropagation();
			if (el.preventDefault) el.preventDefault();
			let self = this,
				listName = data.source_sn + data.db_source_name,
				minWidth = self.getVw(data.db_source_name.length * 10 + 50),
				minHeight = self.getVh(70),
				tableName = self.$refs[data.source_sn][0],
				listDiv = self.$refs[listName][0],
				listDivHeight = listDiv.offsetHeight,
				listLiHeight = listDiv.firstElementChild.firstElementChild.offsetHeight,
				listUlHeight = listDiv.firstElementChild.offsetHeight,
				domHeight = tableName.offsetHeight,
				domWidth = tableName.offsetWidth,
				elLeft = tableName.offsetLeft,
				elTop = tableName.offsetTop,
				domLeft = el.clientX,
				domTop = el.clientY,
				left = elLeft,
				top = elTop,
				height = domHeight,
				width = domWidth,
				posi = {
					offset_x: elLeft,
					offset_y: elTop,
					width: domWidth,
					height: domHeight
				};
			self.chosen = data.source_sn;
			if (minWidth > 200) minWidth = 200;
			//鼠标按下开始缩放表格
			document.onmousemove = function(event) {
				tableName.setCapture && tableName.setCapture();
				self.chosen = data.source_sn;
				event = event || window.event;
				switch (mark) {
				case 'tl':
					left = event.clientX - domLeft + elLeft;
					top = event.clientY - domTop + elTop;
					width = domLeft - event.clientX + domWidth;
					height = domTop - event.clientY + domHeight;
					break;
				case 'tm':
					top = event.clientY - domTop + elTop;
					height = domTop - event.clientY + domHeight;
					break;
				case 'tr':
					top = event.clientY - domTop + elTop;
					width = event.clientX - domLeft + domWidth;
					height = domTop - event.clientY + domHeight;
					break;
				case 'mr':
					width = event.clientX - domLeft + domWidth;
					break;
				case 'br':
					width = event.clientX - domLeft + domWidth;
					height = event.clientY - domTop + domHeight;
					break;
				case 'bm':
					height = event.clientY - domTop + domHeight;
					break;
				case 'bl':
					left = event.clientX - domLeft + elLeft;
					width = domLeft - event.clientX + domWidth;
					height = event.clientY - domTop + domHeight;
					break;
				case 'ml':
					left = event.clientX - domLeft + elLeft;
					width = domLeft - event.clientX + domWidth;
					break;
				}
				//当前宽度小于最小宽度时,返回最小宽度
				if (width < minWidth) width = minWidth;
				//当前高度小于最小宽度时,返回最小高度
				if (height < minHeight) height = minHeight;
				let listHeight = height - 35;
				self.$set(tableName.style, 'top', top + 'px');
				self.$set(tableName.style, 'left', left + 'px');
				self.$set(tableName.style, 'height', height + 'px');
				self.$set(tableName.style, 'width', width + 'px');
				self.$set(self.$refs[listName][0].style, 'width', width + 'px');
				self.$set(self.$refs[listName][0].style, 'height', listHeight + 'px');
				self.instance.repaintEverything();
				if (listDivHeight < listHeight) {
					self.showFiled(data.field, listLiHeight, listHeight, index);
				} else {
					self.hideFiled(
						data.field,
						listLiHeight,
						listUlHeight,
						listHeight,
						index
					);
				}
				self.finalWidth = width;
				self.finalHeight = height;
				self.finalLeft = left;
				self.finalTop = top;
			};

			//鼠标抬起开始缩放表格
			document.onmouseup = function() {
				tableName.releaseCapture && tableName.releaseCapture(); //释放获取鼠标
				document.onmousemove = null;
				document.onmouseup = null;

				let positionData = {
					suffix: data.source_sn,
					height: self.finalWidth,
					width: self.finalHeight,
					offset_x: self.finalLeft,
					offset_y: self.finalTop
				};
				self.positionSave(positionData, posi, data.source_sn, listName);
			};
			tableName.setCapture && tableName.setCapture();
		},
		showFiled(field, liH, divH, index) {
			let num = Math.ceil((divH - liH) / liH);
			let self = this;
			for (let i = 1; i < num - 1; i++) {
				if (i > 0 && field.length > i) {
					self.$set(self.dataModeling[index].field[i], 'fieldShow', false);
				}
			}
			self.instance.repaintEverything();
		},
		hideFiled(fields, liH, ulH, divH, index) {
			let self = this;
			let filedL = fields.length,
				num = Math.ceil(divH / liH);
			for (let i = 1; i < filedL - num + 2; i++) {
				if (filedL - i > 0) {
					self.$set(
						self.dataModeling[index].field[filedL - i],
						'fieldShow',
						true
					);
				}
			}
			self.instance.repaintEverything();
		},
		setTableSize(position) {
			let styleObj = {
				width: position.width + 'px',
				height: position.height + 'px',
				top: position.offset_y + 'px',
				left: position.offset_x + 'px'
			};
			return styleObj;
		},
		setListSize(position) {
			let styleObj = {
				width: position.width + 'px',
				height: position.height - parseInt(this.getVw(45)) + 'px'
			};

			return styleObj;
		},
		requestFailed(info) {
			this.ancelStyle(info);
			this.instanceOut = true;
			this.modifyDeleteShow = false;
			this.editorShow = false;
			// this.$refs.tips.closeTips();
		},
		addDataRelationFailed(str, info) {
			this.tips.show([str]);
			// this.tipsData.text = str;
			// this.tipsData.rel = 'fail';
			// this.$refs.tips.closeTips();
			this.instance.deleteConnection(info);
		},
		createFlow() {
			let self = this;
			this.instance = jsPlumb.getInstance({
				Connector: [
					'Flowchart',
					{
						curviness: 50
					}
				],
				Endpoint: ['Blank', {}],
				HoverPaintStyle: {
					stroke: '#28b3fc',
					lineWidth: 3
				},
				anchor: 'Continuous',
				ConnectionOverlays: [
					[
						'Label',
						{
							label: `<img class='labelImg' id='labelImg' src='${inner}' alt='method'/>`,
							id: 'labelImg',
							cssClass: 'imgLabel'
						}
					],
					[
						'Label',
						{
							label: 'INNER',
							id: 'joinType',
							cssClass: 'typeLabel'
						}
					]
				],
				Container: 'tableContent'
			});
			// 数据刷新之后清除之前的连接线
			if (self.connLine.length != 0) {
				for (let item of self.connLine) {
					if (item != undefined) {
						if (item.endpoints != null) {
							self.instance.deleteConnection(item);
						}
					}
				}
			}
			self.connLine = [];
			self.instance.batch(() => {
				self.registrationFields().then(() => {
					self.registrationLines();
				});
			});
			self.instance.repaintEverything();
			self.isInitialize = false;
			self.instance.fire('jsPlumbDemoLoaded', self.instance);
			self.instance.importDefaults({
				ConnectionsDetachable: false
			}); //再次拖动连接不断开

			// 正在拖动现有连接
			// 注：当此事件触发时，连接的目标端点是jsPlumb用于拖动的瞬态元素，随后在建立或中止连接时将从DOM中删除。
			self.instance.bind('connectionDrag', function(info) {
				self.zIndexDrag = false;
				let addBorder = 'solid 0.0625vw #28b3fc';
				let source = self.$refs[info.sourceId][0];
				let tableSource = self.instanceId[info.sourceId][2];
				self.$refs[tableSource][0].style.border = addBorder;
				switch (source.type) {
				case 'int':
					source.classList.add('intList');
					break;
				case 'char':
					source.classList.add('charList');
					break;
				case 'time':
					source.classList.add('dateList');
					break;
				}
			});

			// 连接拖动结束
			self.instance.bind('connectionDragStop', function(info) {
				let ancelBorder = 'solid 0.0625vw #475566';
				let tableSource = self.instanceId[info.sourceId][2];
				let source = self.$refs[info.sourceId][0];
				let tabSource = self.$refs[tableSource][0];
				tabSource.style.border = ancelBorder;
				switch (source.type) {
				case 'int':
					source.classList.remove('intList');
					break;
				case 'char':
					source.classList.remove('charList');
					break;
				case 'time':
					source.classList.remove('dateList');
					break;
				}
				self.zIndexDrag = true;
			});

			// 当连接建立前
			self.instance.bind('beforeDrop', function(info) {
				let source = info.sourceId;
				let target = info.targetId;
				let tableSource = self.instanceId[source][2];
				let tableTarget = self.instanceId[target][2];
				if (tableSource != tableTarget) {
					//包括是否连接自己和是否同一个表
					console.log('可以连接');
					return true; // 连接会自动建立
				} else {
					return false; // 连接不会建立，注意，必须是false
				}
			});

			//连接
			self.instance.bind('connection', function(connInfo) {
				console.log(self.getConnectEnd);
				if (self.getConnectEnd) { // 现在都搞不清楚为什么要通过这个来判断是否可以连接
					let source = connInfo.connection.sourceId;
					let target = connInfo.connection.targetId;
					let startField = self.instanceId[source][1]; //连接开始字段
					let endField = self.instanceId[target][1]; //连接结束字段
					let startId = self.instanceId[source][2]; //连接开始数据源ID
					let endId = self.instanceId[target][2]; //连接结束数据源ID
					let startName = self.instanceId[source][0]; //连接开始数据源名称
					let endName = self.instanceId[target][0]; //连接结束数据源名称
					let obj = {
						start_source_name: startName,
						end_source_name: endName,
						start_source_id: startId,
						start_field: startField,
						end_source_id: endId,
						end_field: endField
					}
          console.log(self.canAddConnect);
            self.$post('api/addDataRelation', obj).then(
              response => {
                let data = response.data;
                if(data.length === 0) return
                if (response.code == '000000') {
                  let typeName = data.join_type + '-' + data.id;
                  console.log(typeName);
                  self.connLine.push(connInfo.connection);
                  connInfo.connection
                    .getOverlay('joinType')
                    .setLabel(`${typeName}`);
                } else {
                  let str = response.err_msg.split(':')[1];
                  let info = connInfo.connection;
                  self.addDataRelationFailed(str, info);
                }
              },
              error => {
                console.log(error.response.data);
                let str = '连接失败，请重试';
                let info = connInfo.connection;
                self.addDataRelationFailed(str, info);
              }
            );
				}
			});
			//线条删除修改
			self.instance.bind('contextmenu', function(info, event) {
        info.setPaintStyle({ stroke: '#28b3fc'});
        // info.getPaintStyle().stroke ='#28b3fc';
        self.correlationInfo = info;
        self.isBorderShow = true;
        let typeName = info.getOverlay('joinType').label;
        let id = typeName.split('-')[1];
				let jointype = typeName.split('-')[0];
				self.addStyle(info);
				self.msgData = {};
				self.modifyDeleteShow = true;
				event.returnValue = false;
				self.msgData.left = event.clientX;
				self.msgData.top = event.clientY;
				//修改关联
				self.msgData.modifyFun = function() {
					self.editorData = {};
					self.modifyDeleteShow = false;
					self.editorShow = true;
					self.editorData.type = null;
					self.editorData.type = judgeType(jointype)[1];
					self.editorData.top = event.clientY;
					self.editorData.left = event.clientX;
					self.editorData.determineFun = function(data) {
            self.isBorderShow = false;
            info.setPaintStyle({ stroke: '#95a0b6'});
            self.ancelStyle(info);
            self.editorShow = false;
						if (data != null) {
							let typeVal = data.value;
							self
								.$put('api/updateRelation', {
									join_type: typeVal,
									id: id
								})
								.then(
									response => {
										if (response.code == '000000') {
											let type = judgeType(typeVal);
											let imgUrl = type[0];
											let imgAlt = type[1];
											let typeName = typeVal + '-' + id;
											let image = `<img class='labelImg' src='${imgUrl}' alt='${imgAlt}'/>`;
											info.getOverlay('joinType').setLabel(`${typeName}`);
											info.getOverlay('labelImg').setLabel(`${image}`);
											self.tipsData.text = '修改成功';
											self.tipsData.rel = 'success';
										} else {
											this.tips.show([response.err_msg.split(':')[1]]);
											// this.tipsData.rel = 'fail';
											// self.tipsData.text = response.err_msg.split(':')[1];
											// self.tipsData.rel = 'fail';
										}
										self.requestFailed(info);
									},
									error => {
										console.log(error);
										self.tipsData.text = '修改失败，请重试';
										self.tipsData.rel = 'fail';
										self.requestFailed(info);
									}
								);
						}
					};
					self.editorData.cancelFun = function() {
            info.setPaintStyle({ stroke: '#95a0b6'});
            self.isBorderShow = false;
						self.editorShow = false;
						self.ancelStyle(info);
					};
				};

				//删除关联
				self.msgData.deleteFun = function() {
          self.isBorderShow = false;
					self.$put('api/delDataRelation', {
					  id: id
					}).then(response => {
								if (response.code == '000000') {
									self.instance.deleteConnection(info);
									self.tipsData.text = '删除成功';
									self.tipsData.rel = 'success';
								} else {
									self.tipsData.text = response.err_msg.split(':')[1];
									self.tipsData.rel = 'fail';
								}
								self.requestFailed(info);
							},
							error => {
								console.log(error);
								self.tipsData.text = '删除失败,请重试';
								self.tipsData.rel = 'fail';
								self.requestFailed(info);
							});
				};
			});

			//鼠标点击
			self.instance.bind('click', function(connInfo) {
        self.addStyle(connInfo);
			});

			//鼠标移入
			self.instance.bind('mouseover', function(connInfo) {
				if (self.zIndexDrag) {
					self.addStyle(connInfo);
				}
			});

			//鼠标移出
      self.instance.bind('mouseout', function (connInfo) {
        console.log(self.isBorderShow);
        if(self.isBorderShow) {
            return
        }
        if (self.zIndexDrag || self.instanceOut) {
          self.ancelStyle(connInfo);
        }
      });
		},
		registrationFields() {
			let self = this;
			return new Promise(function(resolve) {
				//为每一个表注册连接事件
				self.dataModeling.forEach((dragName, index) => {
					let tabName = dragName.db_source_name; //获取表名
					let tabId = dragName.source_sn; //获取ID
					let tableName = self.$refs[tabId][0];
					let	tabWidth = tableName.offsetWidth;
					let	tabHeight = tableName.offsetHeight;
					let	tabTop = tableName.offsetTop;
					let	tabLeft = tableName.offsetLeft;
					let	listName = tabId + tabName;
					let	listUl = self.$refs[listName][0].firstElementChild;
					let	listHeight = self.$refs[listName][0].offsetHeight;
					let	listLiHeight = listUl.firstElementChild.offsetHeight;
					let	listUlHeight = listUl.offsetHeight;
					if (listUlHeight > listHeight) {
						self.hideFiled(
							dragName.field,
							listLiHeight,
							listUlHeight,
							listHeight,
							index
						);
					}

					//注册拖拽
					self.instance.draggable(self.$refs[tabId], {
						// containment: 'parents', //保证拖动不越界
						// containment: self.$refs.tableRelation, //保证拖动不越界
						// start: function () {},
						drag: function(info, event) {
							self.instance.repaintEverything();
						},
						stop: function(info) {
							let posi = {
								offset_x: tabLeft,
								offset_y: tabTop,
								width: tabWidth,
								height: tabHeight
							};
							let positionData = {
								suffix: info.el.id,
								height: info.el.offsetHeight,
								width: info.el.offsetWidth,
								offset_x: info.finalPos[0],
								offset_y: info.finalPos[1]
							};
							self.instance.repaintEverything();
							self.positionSave(positionData, posi, tabId, listName);
						}
					});

					//为每一个字段注册连接事件
					dragName.field.forEach(item => {
						let name = tabId+tabName + item.Field;
						self.instanceId[name] = [tabName, item.Field, tabId];
						self.instance.makeSource(name, {
							endpoint: 'Blank',
							anchor: 'Continuous',
							connectorStyle: {
								outlineStroke: '#95a0b6',
								strokeWidth: 0
							}
							// maxConnections: -1
						}); //整个节点作为source

						self.instance.makeTarget(name, {
							endpoint: 'Blank',
							anchor: 'Continuous',
							connectorStyle: {
								outlineStroke: '#95a0b6',
								strokeWidth: 0
							}
							// maxConnections: -1
						}); //整个节点作为target
					});
				});
				resolve();
			});
		},
		registrationLines() {
			let self = this;
			console.log(self.getModelPage); // 3 4
			if (self.getModelPage.total == self.getModelPage.current - 1) { //请求次数 == 当前页数-1
				self.relation = [];
				self.relation = self.getRelationData; //relation是判断是否有已经连接过的线存在
        this.$store.commit('dataSource/setConnectEnd', true);//不论有没有连接过的线存在 都要保持可以连接
				// 添加连接线
				if (self.relation==null) { //如果没有连接的线存在 不执行下一步操作
					return;
				}
				if(self.relation.length !=0) { //这里是把已存在的连接刷新页面后继续连接
					self.relation.forEach(point => {
						let end = point.end_source_id+point.end_source_name + point.end_field;
						let start = point.start_source_id +point.start_source_name + point.start_field;
						if (self.$refs[end] == undefined || self.$refs[start] == undefined) {
							self.$put('api/delDataRelation', {
								id: point.id
							})
								.then(response => {});
						} else {
							let type = judgeType(point.join_type);
							let imgUrl = type[0];
							let imgAlt = type[1];
							let typeName = point.join_type + '-' + point.id;
              console.log(typeName);
							let connection = self.instance.connect({
								source: `${start}`,
								target: `${end}`,
								endpoint: 'Blank',
								type: 'Flowchart',
								anchor: 'Continuous',
								ConnectionOverlays: [
									[
										'Label',
										{
											label: `<img class='labelImg' src='${imgUrl}' alt='${imgAlt}'/>`,
                        cssClass: 'imgLabel'
										}
									],
									[
										'Label',
										{
											label: point.join_type,
											labelStyle: {
												opacity: 0
											}
										}
									]
								],
								paintStyle: {
									stroke: '#95a0b6',
									strokeWidth: 2
								}
							});
              console.log(connection);
							self.connLine.push(connection);
							if (connection) {
								let image = `<img class='labelImg' src='${imgUrl}' alt='${imgAlt}'/>`;
								connection.getOverlay('labelImg').setLabel(`${image}`);
								connection.getOverlay('joinType').setLabel(`${typeName}`);
							}
							self.instance.repaintEverything();
						}
					});
					self.canAddConnect = true;
				}
			}
		},
		addStyle(connInfo) {
			let self = this;
			let addBorder = 'solid 0.0625vw #28b3fc';
			let source = self.$refs[connInfo.sourceId][0];
			let target = self.$refs[connInfo.targetId][0];
			let typeSource = source.type;
			let typeTarget = target.type;
			let tableSource = self.instanceId[connInfo.sourceId][2];
			let tableTarget = self.instanceId[connInfo.targetId][2];
			self.$refs[tableSource][0].style.border = addBorder;
			self.$refs[tableTarget][0].style.border = addBorder;
			self.addLiStyle(typeTarget, target);
			self.addLiStyle(typeSource, source);
		},
		ancelStyle(connInfo) {
			let self = this;
			let transBorder = 'solid 0.0625vw transparent';
			let transBg = 'transparent';
			let tableSource = self.instanceId[connInfo.sourceId][2];
			let tableTarget = self.instanceId[connInfo.targetId][2];
			let source = self.$refs[connInfo.sourceId][0];
			let target = self.$refs[connInfo.targetId][0];
			let tabSource = self.$refs[tableSource][0];
			let tabTarget = self.$refs[tableTarget][0];
			let ancelBorder = 'solid 0.0625vw #475566';
			target.style.background = transBg;
			target.style.border = transBorder;
			source.style.background = transBg;
			source.style.border = transBorder;
			tabTarget.style.border = ancelBorder;
			tabSource.style.border = ancelBorder;
		},
		addLiStyle(type, item) {
			switch (type) {
			case 'int':
				item.style.background = '#153325';
				item.style.border = 'solid 0.0625vw #0fff00';
				break;
			case 'char':
				item.style.background = '#182b3f';
				item.style.border = 'solid 0.0625vw #28b3fc';
				break;
			case 'time':
				item.style.background = '#2e3325';
				item.style.border = 'solid 0.0625vw #ffff00';
				break;
			}
		},
		// 缩放百分比下拉框
		toggleOptionsShow() {
			this.isRotate = !this.isRotate;
		},
		hideOptions() {
			this.isRotate = false;
		},
		// 缩放加减
		getScaleValue(scaleValue) {
			this.returnScaleValue = scaleValue;
			this.canvasScaleValue = scaleValue;
			this.scalePercentage = `${scaleValue}%`;
			if (!this.isInitialize) {
				this.scaleEvent(scaleValue);
			}
		},
		zoomSubtract() {
			if (this.returnScaleValue > 10) {
				const scaleValue = this.returnScaleValue - 10;
				this.canvasScaleValue = scaleValue;
				this.scalePercentage = `${scaleValue}%`;
				this.scaleEvent(scaleValue);
			} else {
				return;
			}
		},
		zoomAdd() {
			if (this.returnScaleValue < 100) {
				const scaleValue = this.returnScaleValue + 10;
				this.canvasScaleValue = scaleValue;
				this.scalePercentage = `${scaleValue}%`;
				this.scaleEvent(scaleValue);
			} else {
				return;
			}
		},
		setScaleValue(value) {
			this.scalePercentage = `${value}%`;
			this.canvasScaleValue = value;
			this.isRotate = false;
			console.log('已执行');

			if (!this.isInitialize) {
				this.scaleEvent(value);
			}
		},
		scaleEvent(value) {
			this.scaleNum = value / 100;
			this.instance.setZoom(this.scaleNum, true);
			this.instance.repaintEverything();
			this.$put('api/DataSourceZoom', {
				zoom: this.scaleNum
			}).then(
				response => {
					if (response.code == '000000') {
						this.lastScalingvalue = this.scaleNum;
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// this.tipsData.text = response.err_msg.split(':')[1];
						// this.tipsData.rel = 'fail';
						// this.$refs.tips.closeTips();
						this.scaleNum = this.lastScalingvalue;
					}
				},
				error => {
					console.log(error);
					this.tips.show(['修改位置失败']);
					// this.tipsData.text = '修改位置失败';
					// this.tipsData.rel = 'fail';
					// this.$refs.tips.closeTips();
					this.scaleNum = this.lastScalingvalue;
				}
			);
		},
		refreshEvent() {
			this.requestData().then(() => {
				jsPlumb.ready(() => {
					this.createFlow();
					this.getScaleValue(70);
				});
			});
		}
	},
	watch: {
		chosenTable: {
			handler(val, old) {
				this.chosen = val;
			}
		},
		getModelPage: {
			handler(val, old) {
				if(val.current == old.current){
					this.registrationLines();
				}
			},
			deep:true
		},
    getRelationData: {
      handler(val, old) {
        console.log(val);
        console.log(old);
       if (val.length!==0){
         console.log('执行');
         this.registrationLines();
       }
      },
      deep:true
    },
    // canAddConnect:{
    //   handler() {
    //     this.createFlow();
    //   },
    // }
	},
	mounted() {
	  this.$nextTick(()=>{
			this.refreshEvent();
		});
	},
};
</script>
