<template>
  <div class="dataModeling"
    id="dataModeling">
    <div class="title">
      <span class="vertical"></span>
      <span class="titleText unSelect">数据建模</span>
    </div>
    <DataSource @dataModelingBack="dataModelingEvent"
      @relationshipSuccess="refreshRequestEvent"
      @chosenListBack="chosenListEvent"
      @modelingrRefresh="refreshRequestEvent"
      @requestCompleted='refreshRequestEvent'
      :dataTitle="dataTitle"
      :requestUrl="requestUrl"
      :uploadModeling="modelingData"
      ref="DataSource"></DataSource>
    <TableReLation v-bind="{chosenTable}"
      v-if="relationTable"
      ref="refreshTable"
      @uploadTable="uploadEvent"></TableReLation>
  </div>
</template>
<script>
import TableReLation from '@/pages/DataModeling/components/TableRelation.vue';
import DataSource from '@/pages/DataSource/index.vue';

export default {
	name: 'dataModeling',
	components: { DataSource, TableReLation },
	data() {
		return {
			dataTitle: {
				title: '数据源',
				icon: 'dataBase'

			},
			requestUrl: 'api/modelInfo',
			relationTable: false,
			chosenTable: '-1',
			modelingData: null
		};
	},
	mounted() {},
	methods: {
		dataModelingEvent(data) {
		  return new Promise((resolve, reject) => {
				if (this.relationTable) {
					this.chosenTable = data.source_sn;
				}
				this.relationTable = true;

			});
		},

		chosenListEvent(data) {
			this.chosenTable = data.source_sn;
		},

		refreshRequestEvent(data) {
		  // debugger;
			// let timer = null;
			// this.$nextTick(()=>{
			// 	this.$refs.refreshTable.refreshEvent();
			// });
			// setTimeout(() => {
			// 	// console.log(this.$refs.refreshTable);
			// }, 100);
		},

		uploadEvent(data) {
			this.$refs.DataSource.uploadEvent();
		}
	}
};
</script>
<style scoped>
.dataModeling .title {
  color: #fff;
  position: absolute;
  padding: 3.9vh 0 0 5.1vw;
}

.dataModeling .title .titleText {
  font-size: 0.9vw;
}

.dataModeling .title .vertical {
  width: 0.1vw;
  height: 1.9vh;
  margin-right: 0.3vw;
  display: inline-block;
  vertical-align: -0.2vh;
  background-color: #28b3fc;
}
</style>

